* {
	box-sizing: border-box;
	scrollbar-width: none;
	scroll-behavior: smooth;
}

*::-webkit-scrollbar {
	display: none;
	-webkit-appearance: none;
}

h1, h2, h3, h4, h5, h6, p, li, td, a, span, button, figure {
	font-family: "Red-Hat-Display";
	font-weight: 300;
}


.hero {
	margin: 80px auto 64px auto;

	&__inner {
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	&__logo, .logo {
		display: block;
		margin: 0 auto;
		position: relative;

		&__point, .point {
			width: 14px;
			height: 14px;
			border-radius: 50%;
			position: absolute;
			
			&--blue {
				background-color: $hippie-blue;
				left: -32px;
				top: 16px;
			}
			
			&--violet {
				background-color: $delunge-violet;
				left: -24px;
			}
		}


		&__text {
			font-size: 24px;
			font-weight: 900;
			text-align: center;
		}

	}

}